* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: 'PhageRough';
  src: local('Pacifico Regular'), url('../fonts/PhageRough.woff2');
  font-display: swap;
}

body {
  font-family: 'PhageRough' !important;
  background-color: black;
  letter-spacing: 5px;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.text-large {
  font-size: 40px;
}

@media screen and (max-width:400px) {
  .text-large {
    font-size: 30px;
  }
}

